.beedle__experiences {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}

.beedle__experiences div {
    margin: 0 1rem;
    padding: 10px;
}

.beedle__experiences .title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    margin-top: 20px;
}

.beedle__experiences-topic {
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
    font-size: 1rem;
    width: 100%;
    height: 100%;
}

.beedle__experiences-topic .brief {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 1.5rem;
    width: 100%;
    height: 100%;
}

.beedle__experiences-topic p {
    font-size: 1.5rem;
    /* border: 1px solid; */
    line-height: 40px;
    margin-top: 15px;
    align-self: flex-start;
}

.beedle__experiences-topic ul {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: 80%;
    justify-content: flex-start;
    text-decoration: none;
}

.beedle__experiences-topic li {
    margin: 10px;
    font-size: 1.5rem;
}

.beedle__card-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    /* border: 1px solid; */
}

.beedle__card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: 100px;
    min-width: 50px;
}

.beedle__card img {
    width: 70%;
    height: 50%;
    min-width: 70px;
}

.beedle__ref-container .beedle__ref {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: 100px;
    min-width: 50px;
}

.beedle__ref img {
    width: 100%;
    height: 50%;
    min-width: 500px;
}