.beedle__introduction {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.beedle__introduction div {
    display: flex;
    flex: 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;
    justify-content: center;
    align-items: center;
}

.beedle__introduction div img {
    max-width: 80px;
    min-width: 80px;
}

.beedle__introduction div a:hover {
    padding-bottom: 10px;
}