.beedle__aboutme {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    padding: 0 1rem;
    width: 100%;
    height: 100%;
    font-size: 1.5rem;
}

.beedle__aboutme div {
    line-height: 40px;
    margin: 0 1rem;
    padding: 1rem;
}

.beedle__aboutme p {
    font-size: 1.5rem;
    margin-top: 15px;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    margin-top: 20px;
}