@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.beedle__aboutme {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    padding: 0 1rem;
    width: 100%;
    height: 100%;
    font-size: 1.5rem;
}

.beedle__aboutme div {
    line-height: 40px;
    margin: 0 1rem;
    padding: 1rem;
}

.beedle__aboutme p {
    font-size: 1.5rem;
    margin-top: 15px;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    margin-top: 20px;
}
.beedle__experiences {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}

.beedle__experiences div {
    margin: 0 1rem;
    padding: 10px;
}

.beedle__experiences .title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    margin-top: 20px;
}

.beedle__experiences-topic {
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
    font-size: 1rem;
    width: 100%;
    height: 100%;
}

.beedle__experiences-topic .brief {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 1.5rem;
    width: 100%;
    height: 100%;
}

.beedle__experiences-topic p {
    font-size: 1.5rem;
    /* border: 1px solid; */
    line-height: 40px;
    margin-top: 15px;
    align-self: flex-start;
}

.beedle__experiences-topic ul {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: 80%;
    justify-content: flex-start;
    text-decoration: none;
}

.beedle__experiences-topic li {
    margin: 10px;
    font-size: 1.5rem;
}

.beedle__card-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    /* border: 1px solid; */
}

.beedle__card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: 100px;
    min-width: 50px;
}

.beedle__card img {
    width: 70%;
    height: 50%;
    min-width: 70px;
}

.beedle__ref-container .beedle__ref {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: 100px;
    min-width: 50px;
}

.beedle__ref img {
    width: 100%;
    height: 50%;
    min-width: 500px;
}

.beedle__header {
    display: flex;
}

.beedle__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.beedle__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em
}

.beedle__header-content p {
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 28px;
    color: #fff;
    margin-top: 1.5rem;
}

.beedle__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}

.beedle__header-content__input input {
    flex: 2 1;
    width: 100%;
    min-height: 50px;
    background: #EEC066;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-footer);
    border: none;
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.beedle__header-content__input button {
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;
    background: #FF4920;
    font-weight: bold;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    border: none;
    border-radius: 5px 0px 0px 5px;
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.beedle__header-image {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.beedle__header-image img {
    width: 100%;
    height: 100%;
}

.beedle__header-content .profile img {
    padding: 0 1.5rem;
}

.beedle__header-content .profile p {
    padding: 0 1rem;
    margin-bottom: 1rem;
}

@media screen and (max-width: 1050px) {
    .beedle__header {
        flex-direction: column;
    }
    .beedle__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .beedle__header h1 {
        font-size: 48px;
        line-height: 60px;
    }
    .beedle__header p {
        font-size: 16;
        line-height: 24px;
    }
    .beedle__header-content__input input,
    .beedle__header-content__input button {
        font-size: 16;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .beedle__header h1 {
        font-size: 36px;
        line-height: 48px;
    }
    .beedle__header p {
        font-size: 14px;
        line-height: 24px;
    }
    .beedle__header-content__input input,
    .beedle__header-content__input button {
        font-size: 14px;
        line-height: 16px;
    }
}
.beedle__hobbies {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    padding: 0 1rem;
    width: 100%;
    height: 100%;
    font-size: 1.5rem;
}

.beedle__hobbies div {
    line-height: 40px;
    margin-left: 2rem;
    padding: 1rem;
}

.beedle__hobbies p {
    font-size: 1.5rem;
    margin-top: 15px;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    margin-top: 20px;
}
.beedle__introduction {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.beedle__introduction div {
    display: flex;
    flex: 1 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;
    justify-content: center;
    align-items: center;
}

.beedle__introduction div img {
    max-width: 80px;
    min-width: 80px;
}

.beedle__introduction div a:hover {
    padding-bottom: 10px;
}
.beedle__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.beedle__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.beedle__navbar-links_logo {
    margin-right: 2rem;
}

.beedle__navbar-links_logo img {
    width: 64px;
    height: 64px;
}

.beedle__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.beedle__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.beedle__navbar-links_container p,
.beedle__navbar-sign p,
.beedle__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.beedle__navbar-sign button,
.beedle__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    background: #E86B25;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.beedle__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.beedle__navbar-menu svg {
    cursor: pointer;
}

.beedle__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.beedle__navbar-menu_container p {
    margin: 1rem 0;
}

.beedle__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .beedle__navbar-links_container {
        display: none;
    }
    .beedle__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .beedle__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .beedle__navbar {
        padding: 2rem;
    }
    .beedle__navbar-sign {
        display: none;
    }
    .beedle__navbar-menu_container {
        top: 20px;
    }
    .beedle__navbar-menu_container-links-sign {
        display: block;
    }
}
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient_bg {
    /* ff 3.6+ */
    /* safari 5.1+,chrome 10+ */
    /* opera 11.10+ */
    /* ie 10+ */
    /* global 92%+ browsers support */
    background: radial-gradient(circle at 0% 0%, rgba(180, 186, 191, 1) 2%, rgba(211, 218, 224, 1) 100%);
}

.gradient__text {
    background: var(--gradient-text);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-background-clop: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
    animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@keyframes scale-up-center {
    0% {
        transform: scale(0.5);
    }
    100% {
        transform: scale(1);
    }
}

@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem 2rem;
    }
    .section__margin {
        margin: 4rem 2rem;
    }
}
:root {
    --font-family: 'Manrope', sans-serif;
    --gradient-text: linear-gradient(89.97deg, #E86B25 1.84%, #F49867 102.67%);
    --gradient-bar: linear-gradient(103.22deg, #E86B25 -13.86%, #F49867 99.55%);
    --color-bg: #f2f1f1;
    --color-footer: #031B34;
    --color-blog: #042c54;
    --color-text: #81AFDD;
    --color-subtext: #FF8A71;
}
