.beedle__header {
    display: flex;
}

.beedle__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.beedle__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em
}

.beedle__header-content p {
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 28px;
    color: #fff;
    margin-top: 1.5rem;
}

.beedle__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}

.beedle__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: #EEC066;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-footer);
    border: none;
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.beedle__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background: #FF4920;
    font-weight: bold;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    border: none;
    border-radius: 5px 0px 0px 5px;
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.beedle__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.beedle__header-image img {
    width: 100%;
    height: 100%;
}

.beedle__header-content .profile img {
    padding: 0 1.5rem;
}

.beedle__header-content .profile p {
    padding: 0 1rem;
    margin-bottom: 1rem;
}

@media screen and (max-width: 1050px) {
    .beedle__header {
        flex-direction: column;
    }
    .beedle__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .beedle__header h1 {
        font-size: 48px;
        line-height: 60px;
    }
    .beedle__header p {
        font-size: 16;
        line-height: 24px;
    }
    .beedle__header-content__input input,
    .beedle__header-content__input button {
        font-size: 16;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .beedle__header h1 {
        font-size: 36px;
        line-height: 48px;
    }
    .beedle__header p {
        font-size: 14px;
        line-height: 24px;
    }
    .beedle__header-content__input input,
    .beedle__header-content__input button {
        font-size: 14px;
        line-height: 16px;
    }
}